<template>
  <div v-if="isLoading" class="loading">
    <div class="loading">
      <img
        class="loading-img"
        src="https://hbimg.huabanimg.com/1cd0c5b05f630a1cfd7a056ead30db9f0776cc9e51fe3-MpXEQu"
      />
    </div>
  </div>
  <div class="bookdetail clearfix" v-else>
    <div class="content-left">
      <h1>{{bookdetail.title}}</h1>
      <div>评分:{{bookdetail.rate}}</div>
      <div>作者:{{bookdetail.author}}</div>
      <div>出版日期:{{bookdetail.pubdate}}</div>
      <div>页数:{{bookdetail.pages}}</div>
      <div>出版社:{{bookdetail.press}}</div>
      <div>价格:{{bookdetail.price}}</div>
    </div>
    <div class="content-right">
      <img :src="bookdetail.pic" />
    </div>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  name: "bookDetail",
  data() {
    return {
      bookdetail: {
        title: "",
        pic: "",
        rate: "",
        author: "",
        pubdate: "",
        pages: "",
        press: "",
        price: "",
      },
      isLoading: true,
    };
  },
  methods: {
    getData(id) {
      let url =
        "https://bird.ioliu.cn/v2?url=https://m.douban.com/rexxar/api/v2/market/book/" +
        id;
      axios
        .get(url)
        .then(res => {
          this.bookdetail.title = res.data.data.subject.title;
          this.bookdetail.pic = res.data.data.subject.pic.normal;
          this.bookdetail.rate = res.data.data.subject.rate.value_str;
          this.bookdetail.author = res.data.data.subject.author[0];
          this.bookdetail.pubdate = res.data.data.subject.pubdate[0];
          this.bookdetail.pages = res.data.data.subject.pages[0];
          this.bookdetail.press = res.data.data.subject.press[0];
          this.bookdetail.price = res.data.data.subject.price[0];
          this.isLoading = false;
        })
        .catch(() => {
          return false;
        });
    }
  },
  created() {
    this.getData(this.$route.params.id);
  }
};
</script>
<style scoped>
.clearfix::after {
  content: "";
  display: block;
  clear: both;
}
.bookdetail {
  font-size: 0.3rem;
  width: 100%;
  padding: 1.8rem 0;
  background: url('../../assets/img/book.jpg') no-repeat 100%
}
.content-left,
.content-right {
  float: left;
  padding: 0.1rem 1%;
}
.content-left {
  width: 50%;
  color: #000;
}
.content-right {
  width: 46%;
}
img {
  width: 100%;
  height: 250px;
}
h1,
.content-left div {
  padding-left: 5px;
  padding: 10px 0;
  text-align: center;
}
.loading {
  width: 100vw;
  height: 83vh;
  background-color: rgba(255, 255, 255, 0.5);
  position: fixed;
  top: 1rem;
}
.loading-img {
  padding: 0;
  width: 200px;
  height: 100px;
  border-radius: 10px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
</style>
